@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&display=swap");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
  font-weight: 500;
  font-size: 1rem;
}

ol,
ul,
li {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

.main-container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 1024px) {
  body {
    font-size: 1.1rem;
  }
  .main-container {
    min-height: clamp(60vh, 500px, 80vh);
  }
}
body {
  font-family: "Montserrat", sans-serif;
}

button {
  font-family: inherit;
  font-size: 1rem;
  color: #fff;
  margin: 20px 0;
  background-color: #ff6060;
  padding: 10px 30px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.25);
}

.container-p404 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 130px;
  color: #ff6060;
}
.container-p404__title {
  font-size: 6rem;
  font-weight: 700;
  padding-top: 20px;
}
.container-p404__text {
  text-align: center;
  font-size: 1.125rem;
  font-weight: 500;
}
.container-p404__link {
  color: inherit;
  text-decoration: underline;
  padding-bottom: 20px;
}

@media screen and (min-width: 1024px) {
  .container-p404__title {
    font-size: 18rem;
  }
  .container-p404__txt {
    font-size: 2.25rem;
  }
}
.carousel-info-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 15px;
  color: #ff6060;
}
.carousel-info-container__title {
  font-size: 1.125rem;
  font-weight: 500;
}
.carousel-info-container__location {
  font-size: 0.9rem;
  margin: 15px 0;
}

.stars {
  display: flex;
  flex-wrap: nowrap;
}

.star,
.greystar {
  font-size: 1rem;
  margin-right: 8px;
}

.greystar {
  color: #e3e3e3;
}

.tagsList {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  font-size: 0.7rem;
  margin: 20px 0;
}
.tagsList__tag {
  background-color: #ff6060;
  color: #fff;
  padding: 5px 20px;
  border-radius: 5px;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.rating-host {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (min-width: 1024px) {
  .carousel-info-container__title {
    font-size: 2.25rem;
    font-weight: 500;
  }
  .carousel-info-container__location {
    font-size: 1.125rem;
    margin: 15px 0;
  }
  .carousel-info-container .tagsList__tag {
    padding: 8px 30px;
  }
  .star,
  .greystar {
    font-size: 1.4rem;
  }
  .info-rating-host {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }
  .rating-host {
    flex-direction: column-reverse;
    justify-content: flex-end;
    gap: 3rem;
  }
  .tagsList {
    font-size: 0.8rem;
  }
  .tagsList__tag {
    border-radius: 10px;
  }
}
.header {
  margin: 20px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar__logo {
  height: 45px;
  width: 145px;
}
.navbar__link {
  color: #ff6060;
  text-transform: uppercase;
}

.navbar__link.active {
  text-decoration: underline;
}

.link-wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 25px;
}

@media screen and (min-width: 1024px) {
  .header {
    margin: 20px 100px;
    margin-bottom: 40px;
  }
  .link-wrapper {
    gap: 55px;
  }
}
@media screen and (max-width: 375px) {
  .link-wrapper {
    gap: 10px;
  }
}
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  background-color: #000;
  padding-block: 80px;
  margin-top: 20px;
}
.footer__logo {
  height: 40px;
  width: 125px;
}
.footer__copyright {
  text-align: center;
  color: #fff;
  text-wrap: balance;
  width: 130px;
}

@media screen and (min-width: 1024px) {
  .footer {
    margin-top: 40px;
  }
  .footer__copyright {
    width: 100%;
  }
}
.banner {
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}
.banner__img {
  object-fit: cover;
  height: 110px;
  width: 100%;
  border-radius: 15px;
}
.banner__text {
  margin-left: 15px;
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  font-size: 1.5rem;
  color: #fff;
  text-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  text-wrap: balance;
}

@media screen and (min-width: 1024px) {
  .banner__img {
    height: 220px;
  }
  .banner__text {
    text-wrap: nowrap;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 3rem;
  }
}
.card-container {
  display: grid;
  grid-template-columns: 1fr;
  box-sizing: border-box;
  gap: 20px;
  width: 100%;
  background-color: #f6f6f6;
  border-radius: 25px;
}

.card {
  position: relative;
  height: 250px;
}
.card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.5) 80%);
  opacity: 0.5;
  pointer-events: none;
  border-radius: 15px;
}
.card__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 15px;
}
.card__title {
  position: absolute;
  bottom: 30px;
  margin-left: 15px;
  color: #fff;
  text-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  font-size: 1.1rem;
  font-weight: 500;
  text-wrap: balance;
}

@media screen and (min-width: 768px) {
  .card-container {
    grid-template-columns: 1fr 1fr;
    padding: 25px;
  }
}
@media screen and (min-width: 1024px) {
  .card-container {
    grid-template-columns: 1fr 1fr 1fr;
    padding: 50px;
  }
  .card {
    height: 340px;
  }
}
.collapse-container {
  display: flex;
  flex-direction: column;
}

.collapse {
  box-sizing: border-box;
  width: 100%;
  padding: 10px 15px;
  border-radius: 5px;
  background-color: #ff6060;
  display: flex;
  justify-content: space-between;
}
.collapse__title {
  color: #fff;
}
.collapse__chevron {
  color: white;
  font-size: 1.2rem;
  transition: transform 300ms ease-out;
}
.collapse__chevron--active {
  transform: rotate(-180deg);
}

.logement-collapse {
  width: 100%;
  margin-block: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.apropos-collapse {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 20px;
}

.collapseContent {
  border-radius: 0 0 5px 5px;
  color: #ff6060;
  max-width: 100%;
  opacity: 0;
  transition: all 300ms;
  transform: translateY(-10px);
  transform-origin: top;
  padding-left: 15px;
  height: 0px;
  background-color: #f6f6f6;
}
.collapseContent--active {
  padding-top: 15px;
  height: 100%;
  opacity: 1;
  transform: translateY(0);
}

@media screen and (min-width: 1024px) {
  .collapse {
    padding: 12px 20px;
  }
  .logement-collapse {
    margin: 20px 0 50px 0;
    flex-direction: row;
    width: 100%;
    gap: 75px;
  }
  .logement-collapse .collapse-container {
    width: 50%;
  }
}
.carousel {
  position: relative;
  box-sizing: border-box;
  width: 100%;
}
.carousel__img {
  height: 255px;
  width: 100%;
  object-fit: cover;
  border-radius: 15px;
}
.carousel__current {
  color: #fff;
  position: absolute;
  left: 50%;
  bottom: 7%;
  transform: translate(-50%, -50%);
  text-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.25);
}

.chevrons {
  font-size: 1.4rem;
  font-weight: 700;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  color: #fff;
}
.chevrons__left {
  left: 15px;
}
.chevrons__right {
  right: 15px;
}

@media screen and (min-width: 1024px) {
  .carousel {
    height: 415px;
  }
  .carousel__img {
    height: 100%;
  }
  .chevrons {
    font-size: 3rem;
  }
}
.host {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-block: 10px;
}
.host__name {
  max-width: 70px;
  text-align: right;
}
.host__img {
  object-fit: cover;
  border-radius: 50%;
  height: 2rem;
  width: 2rem;
}

@media screen and (min-width: 1024px) {
  .host {
    margin-block: 0;
  }
  .host__img {
    height: 4rem;
    width: 4rem;
  }
}

