@use "../utils/index" as *;

button {
  font-family: inherit;
  font-size: 1rem;
  color: #fff;
  margin: 20px 0;
  background-color: $primary-color;
  padding: 10px 30px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  box-shadow: 2px 2px 2px 0px rgba($color: #000000, $alpha: 0.25);
}
